@media (max-width: 1300px){
   .aside__menu-item{
      &.item-13,
      &.item-14{
         display: block;
      }
   }
}

@media (max-width: 750px){
   .aside{
      transform: translateX(-100%);
      transition: all .5s;
      opacity: 0;
      position: fixed;
      background-color: #fff;
      box-shadow: 0 0 9px rgba(#000, .5);
      z-index: 5;
      top: 132px;
      &.show{
         transform: translateX(0);
         opacity: 1;
      }
      min-width: 282px;
   }
}

@media (max-width: 500px){
   .aside{
      top: 118px;
   }
}


@media (max-width: 400px){
   .aside__menu{
      padding-left: 15px;
      &::after{
         left: 0;
         transform: none;
      }
   }
   .aside__box{
      padding-left: 15px;
   }
}